#about{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width:90%;
    margin: auto;
    margin-top: 2%;
}
#about .about_photo {
    /* height: 420px; */
    width: 25%;

}
#about .about_info {

    width:65%;
    border: 1px solid black;
    /* padding: 2px;  */
    height: 350px;
    line-height: 30px;
    text-align: left;
    justify-content: center;
    font-size: 20px;
}
.about_info h2{
    text-align: center;
}
#about .about_photo img{
    width:100%;
    border: 1px solid black;
    /* height: 200px; */
}

@media only screen and (max-width : 1024px){

    #about{
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        width:95%;
        margin: auto;
        margin-top: 2%;
        gap:10px
    }
    #about .about_info {

        width:90%;
        border: 1px solid black;
        /* padding: 2px;  */
        height: 390px;
        line-height: 30px;
        text-align: left;
        justify-content: center;
        font-size: 20px;
    }
    .about_info h2{
        text-align: center;
    }
    #about .about_photo img{
        width:100%;
        border: 1px solid black;
        /* height: 200px; */
    }

}
