.mobile{
    background-color: white;
    height: 40vh;
    width: 100vw;
    position: absolute;
    background-color:skyblue;
    right: 0px;
    top: 55px;
    padding: 10px 12px;
    flex-direction: column;
    position: fixed;
    z-index: 9999;
    
}
.close-icon{
 display: flex;
 justify-content: flex-end;
 font-size: 24px;   
}
.mobile-options{
    display: flex;
    flex-direction: column;
    width:100%;
    padding-top:10px;
    justify-content: center;
    align-items: center;
    /* height: 100%; */

}
.mobile-option div{
    font-size: 24px;
    margin: 10px 0px;
    display: flex;
}
.mobile-option div a{
    text-decoration: none;
    color: black;
    display: flex;
}