#skills{
    width: 90%;
    margin: auto;
    margin-top: 4%;
    border: 1px solid black;

}
.skills_section{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 10px;
}
.skills_section div{
    border: 1px solid gray;
    padding: 3% 0px;
    align-items: center;
    /* width: 90%; */
    text-align: center;
    height: 100px;
}
.skills_section div img
{
    width: 15%;
}

@media only screen and (max-width : 1024px){
    #skills{
        width: 90%;
        margin: auto;
        margin-top: 4%;
        /* border: 1px solid black; */
        background-color: black;
    
    }

    .skills_section{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 10px;
    }

    
    

    

}
@media only screen and (max-width : 720px){

    #skills{
        width: 95%;
        margin: auto;
        margin-top: 4%;
        /* border: 1px solid black; */
        background-color: black;
    
    }

    .skills_section{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 10px;
    }
   
   
}

@media only screen and (max-width : 512px){

    #skills{
        width: 95%;
        margin: auto;
        margin-top: 4%;
        /* border: 1px solid black; */
        background-color: black;
    
    }

    .skills_section{
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 10px;
    }
    
   

}

