
.banner_img
{
    position: relative;
    z-index: 10;
    width: 100%;

}
.banner_img img {
    position: relative;
    width: 100%;

}
.text{
    position: absolute;
    top:10%;
    width: 60%;
    left:1%;
    height: 250px;
    /* border: 1px solid white; */
}
.text h1{
  font-size: 50px;
  font-weight: 800;
}

@media only screen and (max-width : 720px){
   
    .text{
        position: absolute;
        width:70%;
        top:5%;
        font-size: large;
        left:2%;
    height: 200px;
    /* border: 1px solid white; */
    }
    .text h1{
        font-size: 40px;
        font-weight: 800;
      }
     
}

@media only screen and (max-width : 512px){
   
    .text{
        position: absolute;
        width:90%;
        top:1%;
        font-size:medium;
    left:2%;
    height: 150px;
    margin: auto;
    /* border: 1px solid white; */
    }

    .text h1{
        font-size: 25px;
        font-weight: 800;
      }
     
}